@import url(https://fonts.googleapis.com/css?family=M+PLUS+Rounded+1c|Rubik&display=swap);
body {
  margin: 0;
  font-family: "Rubik", "M PLUS Rounded 1c", "Segoe UI", "ヒラギノ角ゴ Pro W3",
    "YuGothic", "游ゴシック", "Meiryo", "メイリオ", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

